export const sortChannels = (data) => {
    if (data?.segments) {
        const sortedData = data?.segments.sort((a, b) => {
            let fa = a.inf.title.toLowerCase(), fb = b.inf.title.toLowerCase();
            if (fa < fb) {
                return -1;
            }
            if (fa > fb) {
                return 1;
            }
            return 0;
        });
        return sortedData;
    } else {
        return null;
    }
};